/*
	Global
*/
pre {
  font-size: 1px; }

html {
  font-family: "Montserrat";
  color: #333333;
  width: 100%;
  height: 100%; }

.clickable {
  cursor: pointer; }

body {
  margin: 0;
  width: 100%;
  height: 100%;
  color: #333333; }

/*
	Utilities
*/
/*----------------------------------*    Text
\*----------------------------------*/
/*
	Align
*/
.text-center {
  text-align: center; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

/*
	Decoration
*/
.text-decoration-none {
  text-decoration: none; }

a.text-decoration-none {
  text-decoration: none; }
  a.text-decoration-none:hover, a.text-decoration-none:focus {
    text-decoration: none; }

/*
	Transform
*/
.text-uppercase {
  text-transform: uppercase; }

.text-lowercase {
  text-transform: lowercase; }

.text-capitalize {
  text-transform: capitalize; }

/*
	Variant
*/
.text-small-caps {
  font-variant: small-caps; }

h5.text-small-caps {
  font-size: 25px; }

.text-italic {
  font-style: italic; }

/*
	Weight
*/
.text-thin {
  font-family: 'MontserratLight';
  font-weight: 100; }

.text-light {
  font-weight: 200; }

.text-book {
  font-weight: 300; }

.text-regular {
  font-family: 'Montserrat' !important;
  font-weight: 400; }

.text-medium {
  font-weight: 500; }

.text-semi-bold {
  font-weight: 600; }

.text-bold {
  font-weight: 700; }

.text-black {
  font-weight: 900; }

/*
	Color
*/
.text-color-green {
  color: #62D6C5; }

.text-color-grey {
  color: #797979; }

.text-color-grey-light {
  color: #afafaf; }

.text-color-grey-dark {
  color: #333333; }

.text-color-black {
  color: #000000; }

/*
	Font Size
*/
.text-big {
  font-size: 110px; }
  @media only screen and (max-width: 991px) {
    .text-big {
      font-size: 75px; } }
  @media only screen and (max-width: 767px) {
    .text-big {
      font-size: 55px; } }

/*
 	Text utils
*/
.text-clear-space-below {
  float: left;
  padding-bottom: 40px; }

.text-optional {
  opacity: .5; }

/*
 	Text sizes
*/
.text-size-xxs {
  font-size: 12px; }

.text-size-xs {
  font-size: 14px; }

.text-size-sm {
  font-size: 18px; }

.text-size-smd {
  font-size: 20px; }

.text-size-md {
  font-size: 24px; }

.text-size-lg {
  font-size: 28px; }

.text-size-mlg {
  font-size: 38px; }

.text-size-xlg {
  font-size: 50px; }

/*
  Line height
*/
.text-line-height-1 {
  line-height: 1; }

.text-line-height-lg {
  line-height: 1.67; }

.text-line-height-md {
  line-height: 1.44; }

.text-link {
  cursor: pointer;
  text-decoration: none;
  color: #62D6C5; }
  .text-link:hover {
    color: #2ba794; }

h3 {
  font-size: 32px; }

h4 {
  font-size: 24px; }

/*
	Transforms
*/
.text-underlined {
  text-decoration: underline; }

